import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    allPages: ["Home", "Login"],
    pageState: "Home",
    prevState: ["Home"],
    availableForums: [],
    currentForum: "",
    currentPost: {},
    currentPostID: "",
    forumPosts: [],
    postReplies: [],
    isAddingPost: false,
    isLoggedIn: false,
    currentUserName: "",
    currentUserPFP: ""
  },
  mutations: {
    switchPage(state, newPage) {
      if (newPage == "Home") state.prevState = ["Home"]
      else state.prevState = [...state.prevState, state.pageState]
      state.pageState = newPage
    },
    setForums(state, forumArray) {
      state.availableForums = forumArray
    },
    setForumPosts(state, postArray) {
      state.forumPosts = postArray
    },
    setCurrentForum(state, newForum) {
      state.currentForum = newForum
    },
    setPostStatus(state, newStatus) {
      state.isAddingPost = newStatus
    },
    userLoggedIn(state, userObject) {
      state.isLoggedIn = true
      state.currentUserName = userObject['name']
      state.currentUserPFP = userObject['pfp']
    },
    logout(state) {
      state.isLoggedIn = false
      state.currentUserName = ""
      state.currentUserPFP = ""
      window.location.href = "/"
    },

    setCurrentPost(state, postObject) {
      state.currentPost = postObject
      state.currentPostID = postObject.id
      state.pageState = "Post"
      state.prevState = [...state.prevState, "forum"]
    },
    setAvailableReplies(state, postList) {
      state.postReplies = postList
    }
  },
  actions: {
    setCurrentForumData({ commit }, newForumData) {
      this.state.pageState = "forum"
      commit('setCurrentForum', newForumData[0])
      commit('setForumPosts', newForumData[1])
    },
    updateCurrentForum({ commit }) {
      fetch(`/api/forum/${this.state.currentForum}`, {
        method: "POST",
      })
        .then(res => res.json())
        .then(newForumData => {
          console.log(newForumData);
          commit('switchPage', "forum")
          // commit('setCurrentForum', newForumData[0])
          commit('setForumPosts', newForumData)
        })
    },
    updateCurrentPost({ commit }) {
      fetch(`/api/post/${this.state.currentPostID}`, {
        method: "POST",
      })
        .then(res => res.json())
        .then(newReplies => {
          console.log(newReplies);
          commit('setAvailableReplies', newReplies)
          this.state.pageState = "Post"
          // commit('setCurrentForum', newForumData[0])
        })
    },
    goBack() {
      let newState = this.state.prevState.pop()
      if (this.state.prevState.length == 0) this.state.prevState = ["Home"]
      this.state.pageState = newState
    }
  },
  modules: {},
  getters: {
    pageState(state) {
      return state.pageState;
    },
    forums(state) { return state.availableForums },
    getIsPosting(state) { return state.isAddingPost }
  },
});
