<template>
  <div class="login">
    <div class="titleText"><h1>Please Login With Google To Continue</h1></div>
    <div class="loginButton">
      <GoogleLogin
        :params="params"
        :onSuccess="onSuccess"
        :onFailure="onFailure"
        >Sign In With Google</GoogleLogin
      >
    </div>
  </div>
</template>

<script>
import { GoogleLogin } from "vue-google-login";
export default {
  name: "Login",
  components: {
    GoogleLogin,
  },
  data() {
    return {
      params: {
        client_id:
          "897864354621-el118c9a3qd729f7joaad2fitj5f4t8e.apps.googleusercontent.com",
      },
    };
  },
  methods: {
    onSuccess(googleUser) {
      if (
        googleUser.getBasicProfile().getEmail().indexOf("@nayland.school.nz") ==
        -1
      ) {
        alert("Please Log In With A Nayland College Account!");
        return 0;
      }
      const auth_token = googleUser.getAuthResponse().id_token;
      const data = { token: auth_token };
      fetch("/api/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
      })
        .then((res) => res.json())
        .then((userObject) => {
          this.$store.commit("userLoggedIn", userObject);
          this.$store.commit("switchPage", "Home");
        });
    },
    onFailure() {
      console.log("fail");
    },
  },
};
</script>

<style scoped>
button {
  background-color: var(--light-second-color);
  border: solid;
  color: var(--dark-color);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 20px;
}

.login {
  display: grid;
  place-items: center;
}

.titleText {
  display: block;
}

.loginButton {
  display: block;
}
</style>